import styled from "styled-components";

const Parallax = styled.div`
  position: relative;
  min-height: 100vh;
  &:after {
    content: " ";
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: grey;
    background-size: 100%;
    transform: translateZ(-1px) scale(1.5);
    z-index: -1;
    ${props =>
      props.backgroundUrl && `background: url(${props.backgroundUrl});`};
  }
`;

export default Parallax;



